.container {
  margin: 0 auto;
  max-width: 1280px;
  width: 95%;
}

@media only screen and (min-width: 990px) {
  .container {
    width: 95%;
  }
  .container > .row {
    margin-left: -0.75rem;
    margin-right: -0.75rem;
  }
}

@media only screen and (min-width: 1200px) {
  .container {
    width: 90%;
  }
}

.row {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  margin: 0 0 20px;
}

.row > .col > .row {
  margin: 0;
}

.row > .col > .row > .col:first-child {
  padding-top: 0;
}

.row > .col > .row > .col:last-child {
  padding-bottom: 0;
}

.row.center {
  justify-content: center;
}

.row:after {
  content: "";
