  /* Reset top tooltip */
  margin-left: initial;
  bottom: -30px;
  left: 20px;
}

.tooltip.tooltip-right .tooltip-text::after {
  /* Reset top tooltip */
  border-top-color: transparent;
  margin-left: initial;
  left: initial;
  top: 50%;
  right: 100%;
  margin-top: -5px;
  border-right-color: black;
}

/**
 * Left Tooltip
 */
