  /* Reset top tooltip */
  margin-left: initial;
  left: initial;
  bottom: -30px;
  right: 20px;
}

.tooltip.tooltip-left .tooltip-text::after {
  /* Reset top tooltip */
  border-top-color: transparent;
  margin-left: initial;
  top: 50%;
  left: 100%;
  margin-top: -5px;
  border-left-color: black;
}

/**
 * Bottom Tooltip
 */
.tooltip.tooltip-bottom .tooltip-text {
  /* Reset top tooltip */
  bottom: initial;
  width: 120px;
  top: 100%;
  left: 50%;
  margin-left: -60px;
  /* Use half of the width (120/2 = 60), to center the tooltip */
